.gameboard-wrapper {
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  outline: none;
}

.gameboard-ground {
  height: 100px;
  /* background-image: url(../../../assets/bg.svg); */
  /* background-position: bottom; */
  position: absolute;
  background-color: green;
}

.gameboard-foreground {
  width: 100%;
  overflow: hidden;
  position: relative;
  outline: none;
  cursor: none;
}

.player-sprite {
  position: absolute;
  visibility: hidden;
  overflow-x: hidden;
  border-radius: 50%;
  background: blue;
}

/* .player-sprite img {
  height: 100%;
  width: auto;
  position: absolute;
} */

.enemy-sprite {
  position: absolute;
  visibility: hidden;
  overflow-x: hidden;
  background-color: red;
}

/* .enemy-sprite img {
  height: 100%;
  width: auto;
  position: absolute;
} */

.clouds {
  position: absolute;
  width: 100px;
  background-repeat: no-repeat;
}

.clouds-1-sprite {
  background-image: url('/svg/projects/pixelated/clouds-1.svg');
  height: 44px;
  -webkit-animation: cloud-animation 120s infinite;
          animation: cloud-animation 120s infinite;
}
/* .clouds-1-sprite:nth-child(n + 1) {
  animation-delay: 60s;
} */

.clouds-2-sprite {
  background-image: url('/svg/projects/pixelated/clouds-2.svg');
  height: 56px;
  -webkit-animation: cloud-animation 150s infinite;
          animation: cloud-animation 150s infinite;
}
/* .clouds-2-sprite:nth-child(n + 1) {
  animation-delay: 75s;
} */

.clouds-3-sprite {
  background-image: url('/svg/projects/pixelated/clouds-3.svg');
  height: 25px;
  -webkit-animation: cloud-animation 140s infinite;
          animation: cloud-animation 140s infinite;
}
/* .clouds-3-sprite:nth-child(n + 1) {
  animation-delay: 70s;
} */

@-webkit-keyframes cloud-animation {
  0% { left: 2000px; }
  20% {
    left: -500px;
    opacity: 1;
  }
  21% {
    opacity: 0;
  }
  22% {
    left: 10000px;
    opacity: 0;
  }
  23% {
    opacity: 1;
  }
  100% { left: -500px; }
}

@keyframes cloud-animation {
  0% { left: 2000px; }
  20% {
    left: -500px;
    opacity: 1;
  }
  21% {
    opacity: 0;
  }
  22% {
    left: 10000px;
    opacity: 0;
  }
  23% {
    opacity: 1;
  }
  100% { left: -500px; }
}

.box {
  position: absolute;
}
/* .box-sprite-1 {
  height: 35px;
  width: 35px;
  background-image: url(../../../assets/box.svg);
} */

.score-coin {
  background-image: url('/svg/projects/pixelated/pixel.svg');
  background-repeat: no-repeat;
  width: 20px;
  height: 20px;
  top: 0;
  left: 0;
  position: absolute;
  -webkit-animation: coin-animation 1000ms infinite;
          animation: coin-animation 1000ms infinite;
}

.score-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  margin: 15px 0 0 30px;
  font-size: 20px;
  position: relative;
  padding-left: 25px;
  color: white;
}

.timer {
  display: -ms-flexbox;
  display: flex;
  color: white;
  position: absolute;
  right: 30px;
  top: 15px;
  font-size: 20px;
  -ms-flex-align: end;
      align-items: flex-end;
}

.timer svg {
  fill: white;
  margin-right: 5px;
}

@-webkit-keyframes coin-animation {
  0% {
    top: -2px;
  }
  50% {
    top: 2px;
  }
  100% {
    top: -2px;
  }
}

@keyframes coin-animation {
  0% {
    top: -2px;
  }
  50% {
    top: 2px;
  }
  100% {
    top: -2px;
  }
}


.game-menu {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  z-index: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.game-menu img {
  width: 600px;
  margin-bottom: 50px;
}

.game-menu div {
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.game-menu button {
  padding: 12px 72px;
  border-radius: 30px;
  background: 0;
  border: 1px solid white;
  color: white;
  font-size: 22px;
  margin: 12px 0;
  -webkit-transition: background 0.2s;
  -o-transition: background 0.2s;
  transition: background 0.2s;
  outline: none;
  letter-spacing: 1px;
  width: 250px;
}

.game-menu button:hover {
  background: #5A89AC;
  cursor: pointer;
}

.pause-button {
  width: 25px;
  height: 25px;
  position: absolute;
  left: calc(50% - 12.5px);
  top: 15px;
  background-image: url('/svg/projects/pixelated/pause.svg');
  background-repeat: no-repeat;
  cursor: pointer;
}

.show-mouse {
  cursor: auto;
}

.control-menu {
  -ms-flex-direction: column;
      flex-direction: column;
}

.control-menu div {
  color: white;
  -ms-flex-direction: initial;
      flex-direction: initial;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 10px;
}



