.header-wrapper {
  width: 100%;
  position: fixed;
  z-index: 2000;
}

.header {
  display: -ms-flexbox;
  display: flex;
  max-width: 1250px;
  margin: 0 auto;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0px 20px;
}

@media (max-width: 400px) {
  .header {
    padding: 0px 5px;
  }
}

.nav {
  display: -ms-flexbox;
  display: flex;
}

.nav li {
  color: #ddd;
  list-style: none;
}

.active {
  color: white;
}



