.home-wrapper {
  background-color: #222831;
}

.home-wrapper section > div {
  opacity: 0;
  -webkit-transform: translateY(20px);
      -ms-transform: translateY(20px);
          transform: translateY(20px);
  -webkit-transition: all 0.5s ease 0s;
  -o-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
}

.home-wrapper section.active > div {
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.hero {
  height: 400px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: center;
      align-content: center;
  background-color: #393E46;
  -webkit-clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 90%);
          clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 90%);
}

@media (max-width: 767px) {
  .hero {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }
}

.hero-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.hero-wrapper h1 {
  font-size: 36px;
  width: 100%;
  margin: 0;
}

.hero-wrapper span {
  font-size: 26px;
  letter-spacing: 2px;
  font-weight: 100;
  border-right: 3px solid rgba(255,255,255,0.8);
  -webkit-animation: typist 800ms steps(44) infinite normal;
          animation: typist 800ms steps(44) infinite normal;
  max-width: 0px;
  overflow: hidden;
  white-space: nowrap;
}

@-webkit-keyframes typist {
  0% { border-right: 3px solid rgba(255,255,255,0.8); }
  50% { border-right: 3px solid rgba(255,255,255,0); }
}

@keyframes typist {
  0% { border-right: 3px solid rgba(255,255,255,0.8); }
  50% { border-right: 3px solid rgba(255,255,255,0); }
}

.about {
  width: 100%;
  padding: 100px 0 30px 0;

  background-color: #393E46;
  -webkit-clip-path: polygon(0 10%, 100% 0%, 100% 100%, 0 100%);
          clip-path: polygon(0 10%, 100% 0%, 100% 100%, 0 100%);
}

.about-wrapper {
  max-width: 800px;
  margin: 125px auto 30px;
  padding: 0 10px;
}

.about-wrapper p {
  text-align: center;
}

.profile-image {
  position: relative;
  z-index: 1;
}

.profile-image img {
  width: 250px;
  margin: auto;
  border-radius: 50%;
  position: absolute;
  top: -100px;
  right: 0;
  left: 0;
  -webkit-box-shadow: 0 4px 8px rgba(0,0,0,0.28);
          box-shadow: 0 4px 8px rgba(0,0,0,0.28);
}

@media (max-width: 767px) {
  .profile-image img {
    width: 200px;
    top: -60px;
  }
}

.portfolio {
  margin: 100px 0 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.portfolio-cta {
  padding: 12px 36px;
  border: 3px solid #FFF;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  display: block;
  margin: 75px auto 0;
  font-size: 18px;
}

.portfolio-cta:hover {
  border: 3px solid #00ADB5;
}



